@import (css) url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700');
@import (css) url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,600;0,700;0,900');


@font-face {
	font-family: 'icons';
	src: url('/fnt/icons.eot');
	src: url('/fnt/icons.eot?#iefix') format('eot'),
		url('/fnt/icons.woff2') format('woff2'),
		url('/fnt/icons.woff') format('woff'),
		url('/fnt/icons.ttf') format('truetype'),
		url('/fnt/icons.svg#icons') format('svg');
}
