  -webkit-text-fill-color: currentColor;
  text-size-adjust: 100%;
}
*::before,
*::after {
  all: unset;
  box-sizing: border-box;
  display: block;
  word-break: break-word;
  background: no-repeat center / contain;
  -webkit-text-fill-color: currentColor;
  text-size-adjust: 100%;
  display: inherit;
}
*::placeholder {
  all: unset;
  box-sizing: border-box;
  display: block;
  word-break: break-word;
  background: no-repeat center / contain;
